.faq-container {
  max-width: 600px;
  margin: 20px auto;
  font-family: Arial, sans-serif;
  margin-left: 115px;
}


.faq-heading {
  color: orange;
  font-size: 30px;
  text-align: left;
  margin-bottom: 24px;
  font-family: 'Lilita One';
  font-weight: 400;
  margin-left: 0px;
}

.faq-list {
display: flex;
flex-direction: column;
gap: 10px;
}

.faq-item {
  border: 1px solid #ccc;
  border-radius:18px;
  overflow: hidden;
  width: 1250px
}


.faq-question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  cursor: pointer;
  background-color: var(--bg-color);
  font-weight: 400;
  transition: background-color 0.3s ease;
  font-size: 24px;
  font-family:Geologica;
  
}

.faq-question:hover {
  background-color: var(--item-color);
}


.faq-answer {
  padding: 15px;
 font-weight: 100;
  color: var(--text-color);
  font-size: 22px;
  font-family: Geologica;
  line-height: 1.6;
  background-color: var(--bg-color);
  
}
 

 /* Responsive Styles */

/* For screens 1024px and smaller */
@media (max-width: 1024px) {
.faq-container {
  max-width: 90%;
  margin: 20px auto;
  margin-left: 0;
}
.faq-heading {
  font-size: 28px;
  margin-left: 10px;
}
.faq-item {
  width: 100%;
}
.faq-question {
  font-size: 22px;
}
.faq-answer {
  font-size: 20px;
}
}
@media (min-width:768px) and (max-width: 1024px) {
/* @media (max-width: 768px) { */
.faq-heading {
  font-size: 26px;
  margin-left: 5px;
}
.faq-container {
  max-width: 90%;
  margin: 20px auto;
  margin-left: 38px;
}
.faq-question {
  font-size: 20px;
}
.faq-answer {
  font-size: 18px;
}
}

/* For screens 426px and smaller */
@media (max-width: 426px) {
.faq-container {
  max-width: 100%;
  margin: 10px auto;
  padding: 10px;
}
.faq-heading {
  font-size: 24px;
  text-align: center;
}
.faq-list {
  flex-direction: column; /* Stack items vertically */
}
.faq-item {
  width: 100%;
}
.faq-question {
  font-size: 18px;
  padding: 10px;
}
.faq-answer {
  font-size: 16px;
  padding: 10px;
}
}

/* For screens 376px and smaller */
@media (max-width: 376px) {
.faq-heading {
  font-size: 22px;
}
.faq-question {
  font-size: 16px;
}
.faq-answer {
  font-size: 14px;
}
}

/* For screens 320px and smaller */
@media (max-width: 320px) {
.faq-heading {
  font-size: 20px;
}
.faq-question {
  font-size: 14px;
  padding: 8px;
}
.faq-answer {
  font-size: 12px;
  padding: 8px;
}
}